import React, { Component } from 'react';
import NavBar from 'react-responsive-menubar/lib/NavBar';
import './App.css'
import firebase from "firebase"
//import StyledFirebaseAuth from "react-firebaseui/StyledFirebaseAuth"
 
class Navbar extends Component {
 
  state = {showSideNav:false}
 
  handleSideNavToggle = ()=>{
      this.setState((currentState)=>{
          return {showSideNav: !currentState.showSideNav}
      })
  }
 
 render(){
   //Position of navbar container (header in this case) should be set torelative.
  return(
      <header className="header" style={{position:"relative"}}>
          <NavBar 
          handleSideNavToggle={this.handleSideNavToggle} 
          //logo={'https://www.zilliondesigns.com/images/portfolio/healthcare-hospital/iStock-471629610-Converted.png'} 
          logo={'https://wallpaperplay.com/walls/full/b/d/1/58065.jpg'}
          showSideNav={this.state.showSideNav} 
          logoStyles={{height:"65px", width:"85px", valign:"center", position:"center"}} 
          navBarStyles={{boxShadow:"10"}} 
          linkStyles={{color:"blue", fontWeight:"bold"}}>
 
              <a href="/"></a>
              <a href="/"></a>
              <a href="/"></a>
              <a href="/"></a>
              <a href="/"></a>
              <a href="/"></a>
              <a href='/'></a>
              <h7>Welcome {firebase.auth().currentUser.displayName}</h7>
              <img alt="profile picture" height="25px" width="25px" src={firebase.auth().currentUser.photoURL}/>
              <button onClick={() => firebase.auth().signOut()}>Sign out!</button>
              <a href="/"></a>
 
          </NavBar>
      </header>
  )
 }
}
export default Navbar;