import React, { Component } from "react"
//import './App.css'
import {
  Route,
  NavLink,
  HashRouter
} from "react-router-dom";
import app from './App_original'
import mapi from './maptestgeo'
import datatable from "./data_table"
import piechart from "./piechart"
import barchart from "./barchart"
import firebase from "firebase"
import StyledFirebaseAuth from "react-firebaseui/StyledFirebaseAuth"
import './firebase'
 
class App extends Component {
  state = { isSignedIn: false }
  
  uiConfig = {
    signInFlow: "popup",
    signInOptions: [
      firebase.auth.GoogleAuthProvider.PROVIDER_ID,
      /*firebase.auth.FacebookAuthProvider.PROVIDER_ID,
      firebase.auth.TwitterAuthProvider.PROVIDER_ID,
      firebase.auth.GithubAuthProvider.PROVIDER_ID,
      firebase.auth.EmailAuthProvider.PROVIDER_ID*/
    ],
    callbacks: {
      signInSuccess: () => true
    }
  }

  componentDidMount = () => {
    firebase.auth().onAuthStateChanged(user => {
      this.setState({ isSignedIn: !!user })
      console.log("user", user)
    })
  }
  render() {
    return (
      <div>
        {this.state.isSignedIn ? (
          <HashRouter>
          <div>
            <h1>AROGYAM</h1>
            <ul className="header">
              <li><NavLink to="/mapi">Map</NavLink></li>
              <li><NavLink to="/barchart">Bar Chart</NavLink></li>
              <li><NavLink to="/piechart">Pie Chart</NavLink></li>
              <li><NavLink to="/datatable">Manage Users</NavLink></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li>Welcome {firebase.auth().currentUser.displayName}</li>
              <img alt="profile picture" height="25px" width="25px" src={firebase.auth().currentUser.photoURL}/>
              <li></li>
              <button onClick={() => firebase.auth().signOut()}>Sign out!</button>
            </ul>
          </div>
          <div className="content">
              <Route path="/mapi" component={mapi}/>
              <Route path="/barchart" component={barchart}/>
              <Route path="/piechart" component={piechart}/>
              <Route path="/datatable" component={datatable}/>
            </div>
        </HashRouter>
        ) : (
          <StyledFirebaseAuth
            uiConfig={this.uiConfig}
            firebaseAuth={firebase.auth()}/>
        )}
      </div>
    );
  }
}
 
export default App;